// 储存路由数据
const STORE_MENUS = (state, listData) => {
    state.menus = listData;
};

//添加路由
const APPEND_MENU = (state, routeData) => {
    state.menus.push(routeData);
}

//编辑路由
const UPDATE_MENU = (state, routeData) => {
    let route = state.menus.find((route) => route.route_id === routeData.route_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(route, routeData);
};

//删除路由
const DELETE_MENU = (state, route_id) => {
    let index = state.menus.findIndex((route) => route.route_id === route_id);
    state.menus.splice(index, 1);
}

export default {
    STORE_MENUS,
    APPEND_MENU,
    UPDATE_MENU,
    DELETE_MENU,
}