const allRoutes = (state, getters) => {
    // 添加根基分类
    return [{
        route_name: '全部路由',
        route_id: 0,
        permission_id: 0,
        route_alias: "",
        route_full_path: "",
        route_path: "",
        children: getters.treeRoutes
    }];
};

//转换为树形结构
const parseToTree = function (origin, list) {
    return list.map((parent) => {
        let children = origin.filter((child) => child.parent_id === parent.permission_id);
        if (children.length) {
            return { ...parent, children: parseToTree(origin, children) }
        } else {
            return { ...parent }
        }
    });
}
// 路由--树形结构
const treeRoutes = (state) => {
    let cate_1st = state.routes.filter((route) => route.parent_id === 0);
    //深拷贝，防止修改原始数组
    return parseToTree(state.routes, cate_1st);
}
// 根据路由 route_id 获取完整路由地址
const route_full_path = (state) => {
    return (route_id) => {
        // route_id 空值
        if (!route_id) {
            return '';
        }
        // route_id 有效
        let { route_full_path } = state.routes.find((route) => route.route_id === route_id);
        return route_full_path;
    }
}

export default {
    allRoutes,
    treeRoutes,
    route_full_path,
}