import { Menu } from '@/api/';

// 加载所有菜单
const loadMenuList = async ({ commit }, query) => {
    let { status, data } = await Menu.loadAll(query);
    if (status) {
        // 储存到state
        commit('STORE_MENUS', data);
    }
    return { status, data };
};

//添加菜单
const createMenu = async ({ commit }, menuData) => {
    let { status, msg, data } = await Menu.insert(menuData);
    if (status) {
        // 储存到state
        commit('APPEND_MENU', { ...data, ...menuData });
    }
    return { status, msg, data };
};

//编辑菜单
const editMenu = async ({ commit }, menuData) => {
    let { status, msg } = await Menu.update(menuData.route_id, menuData);
    if (status) {
        // 储存到state
        commit('UPDATE_MENU', menuData);
    }
    return { status, msg };
};

//删除菜单
const removeMenu = async ({ commit }, menuData) => {
    let { menu_id, permission_id } = menuData;
    let { status, msg } = await Menu.remove(menu_id, { permission_id });
    if (status) {
        // 储存到state
        commit('DELETE_MENU', menu_id);
    }
    return { status, msg };
};

export default {
    loadMenuList,
    createMenu,
    editMenu,
    removeMenu,
}
